.addImages {
    background-color: rgba(0, 0, 0, 0.829);
    backdrop-filter: blur(9px);
    position: fixed;
    height: 100vh;
    width: 100%;
    z-index: 400;
}

.card {
    width: 300px;
    top: 10%;
    padding: 1.7rem;
    max-height: 400px;
}

.submitBtn {
    display: block;
    position: absolute;
    left: 38%;
    bottom: 20px;

}

.cancel {
    cursor: pointer;
    text-align: end;
    margin-right: 5px;
    font-size: 20px;
    font-weight: 600;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

img#output {
    display: block;
    margin: 0 auto;
}

.uploadImg {
    cursor: cell;
}